import React,{useState , useEffect} from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let 박스 = styled.div`
  padding : 20px;
`;
let 제목 = styled.h4`
  font-size : 25px;
  color : ${ props => props.색상 };
`;
//옛날버전
// class Detail2 extends React.Component {
//     componentDidMount(){
//       //Detail2 컴포넌트가 Mount 되고나서 실행할 코드
//     }
//     componentWillUnmount(){
//       //Detail2 컴포넌트가 Unmount 되기전에 실행할 코드
//     }
//   }

function Info(props){
    return (
      <p>재고 : {props.재고}</p>
    )
  }

function Detail(props){

    let [ alert, alert변경 ] = useState(true);
    let [inputData, inputData변경] = useState('');
    // useEffect 는 업데이트 될 때도 실행도니다. 
    useEffect(()=>{
        //코드를 적습니다 여기 2초후 재고없음 삭제
        let timer = setTimeout( ()=>{ alert변경(false)} ,2000);

        //사라질때 action 타이머는 시간 안에 다른 이벤트 발생시 문제가 될 수 있으므로 끝날때는 제거해준다. ()=>{} 이건 콜백함수
        return ()=>{ clearTimeout(timer) };
      },[alert] ); // alert 변경이 발생할 때ㅁ만

    let history = useHistory();
    let {id} = useParams();
    let 찾은상품 = props.shoes.find(function(상품){
        return 상품.id == id
      });

    return (
        <div className="container">
        <박스>
            <제목 className="red">상세페이지</제목>
            {
                alert === true
                ?(
                    <div className="my-alert">
                        <p>재고가 얼마 남지 않았습니다</p>
                    </div>
                )
                : null

            }
           <input onChange={ (e)=>{ inputData변경(e.target.value) }}/>
        </박스>
        { inputData }
        <div className="row">
            <div className="col-md-6">
            <img src='https://codingapple1.github.io/shop/shoes1.jpg' width="100%" />
            </div>
            <div className="col-md-6 mt-4">
            <h4 className="pt-5">{찾은상품.title}</h4>
            <p>{찾은상품.content}</p>
            <p>{찾은상품.price}</p>

            <Info 재고 = {props.재고} ></Info>

            <button className="btn btn-danger" onClick={ ()=>{ props.재고변경([9,10,11]) } } >주문하기</button> 
            <button onClick={ ()=>{ history.goBack() } } className="btn btn-danger" >뒤로가기</button> 
            </div>
        </div>
    </div>  
  )
};

export default Detail 